
import ContactForm from "@/components/ContactForm";
import { therapySections, therapyContent } from "@/constants/content";
import FAQSection from "@/components/FAQSection";

const Therapy = () => {
  return (
    <div>
      <section className="bg-gradient-to-r from-soft-purple to-soft-blue py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h1 className="text-4xl md:text-5xl font-playfair font-bold text-center mb-6">
            {therapyContent.hero.title}
          </h1>
        </div>
      </section>

      <section className="py-16">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          {therapySections.map((section, index) => (
            <div key={index} className={`flex flex-col ${index % 2 === 0 ? 'md:flex-row' : 'md:flex-row-reverse'} md:items-center mb-16 gap-8`}>
              <div className="md:w-1/2">
                <img
                  src={section.image}
                  alt={section.title}
                  className="rounded-lg shadow-md w-full h-[300px] object-cover"
                />
              </div>
              <div className="md:w-1/2">
                <h2 className="text-2xl font-playfair font-bold mb-4">{section.title}</h2>
                <p className="text-gray-600">{section.content}</p>
              </div>
            </div>
          ))}
        </div>
      </section>

      <FAQSection />
      
      <ContactForm />
    </div>
  );
};

export default Therapy;
