
import { Button } from "@/components/ui/button";
import { useNavigate } from "react-router-dom";
import { Check, Users } from "lucide-react";
import { features, testimonials, homeContent } from "@/constants/content";
import FAQSection from "@/components/FAQSection";

const Home = () => {
  const navigate = useNavigate();

  return (
    <div>
      <section className="bg-soft-peach py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-4xl md:text-5xl font-playfair font-bold mb-6">{homeContent.hero.title}</h1>
            <p className="text-xl text-gray-600 mb-8">{homeContent.hero.subtitle}</p>
            <Button onClick={() => navigate('/contact')} size="lg" className="bg-primary hover:bg-primary/90">
              {homeContent.hero.cta}
            </Button>
          </div>
        </div>
      </section>

      <section className="py-16 bg-soft-green">
        <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl font-playfair font-bold mb-6">{homeContent.mission.title}</h2>
          <p className="text-lg text-gray-600">
            {homeContent.mission.content}
          </p>
        </div>
      </section>

      <section className="py-16 bg-soft-yellow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-3xl font-playfair font-bold text-center mb-12">{homeContent.features.title}</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {features.map((feature, index) => (
              <div key={index} className="bg-white p-6 rounded-lg shadow-sm">
                <div className="flex items-center mb-4">
                  <Check className="w-6 h-6 text-primary mr-2" />
                  <h3 className="font-semibold text-lg">{feature.title}</h3>
                </div>
                <p className="text-gray-600">{feature.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className="py-16 bg-soft-purple">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-3xl font-playfair font-bold text-center mb-12">{homeContent.testimonials.title}</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {testimonials.map((testimonial, index) => (
              <div key={index} className="bg-white p-6 rounded-lg">
                <p className="text-gray-600 mb-4 italic">"{testimonial.text}"</p>
                <div className="flex items-center">
                  <Users className="w-5 h-5 text-primary mr-2" />
                  <p className="font-semibold">{testimonial.author}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className="py-16 bg-soft-pink">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h2 className="text-3xl font-playfair font-bold mb-6">{homeContent.callToAction.title}</h2>
            <p className="text-lg text-gray-600 mb-8 max-w-2xl mx-auto">
              {homeContent.callToAction.content}
            </p>
            <Button onClick={() => navigate('/contact')} size="lg" className="bg-primary hover:bg-primary/90">
              {homeContent.callToAction.buttonText}
            </Button>
          </div>
        </div>
      </section>
      
      <FAQSection />
    </div>
  );
};

export default Home;
