
import ContactForm from "@/components/ContactForm";
import { aboutContent } from "@/constants/content";
import FAQSection from "@/components/FAQSection";

const About = () => {
  return (
    <div>
      <section className="bg-soft-pink py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h1 className="text-4xl md:text-5xl font-playfair font-bold text-center mb-6">{aboutContent.hero.title}</h1>
        </div>
      </section>

      <section className="py-16">
        <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="prose prose-lg mx-auto">
            <h2 className="text-3xl font-playfair font-bold mb-6">{aboutContent.certifications.title}</h2>
            <ul className="list-disc pl-6 mb-8">
              {aboutContent.certifications.items.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>

            <h2 className="text-3xl font-playfair font-bold mb-6">{aboutContent.mission.title}</h2>
            {aboutContent.mission.paragraphs.map((paragraph, index) => (
              <p key={index} className="text-gray-600 mb-8">{paragraph}</p>
            ))}
          </div>
        </div>
      </section>

      <FAQSection />
      
      <ContactForm />
    </div>
  );
};

export default About;
