
import { Link } from 'react-router-dom';
import { Mail, Phone, Facebook, Instagram, Linkedin } from 'lucide-react';
import { footerContent, navigation, siteInfo } from '@/constants/content';

const Footer = () => {
  return (
    <footer className="bg-soft-purple pt-16 pb-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <div>
            <h3 className="text-lg font-semibold mb-4">{footerContent.navigation.title}</h3>
            <div className="space-y-3">
              <Link to="/" className="block text-gray-600 hover:text-primary">{navigation.home}</Link>
              <Link to="/about" className="block text-gray-600 hover:text-primary">{navigation.about}</Link>
              <Link to="/therapy" className="block text-gray-600 hover:text-primary">{navigation.therapy}</Link>
            </div>
          </div>
          <div>
            <h3 className="text-lg font-semibold mb-4">{footerContent.contact.title}</h3>
            <div className="space-y-3">
              <a href={`tel:${siteInfo.phone}`} className="flex items-center text-gray-600 hover:text-primary">
                <Phone className="w-5 h-5 mr-2" />
                {siteInfo.phone}
              </a>
              <a href={`mailto:${siteInfo.email}`} className="flex items-center text-gray-600 hover:text-primary">
                <Mail className="w-5 h-5 mr-2" />
                {siteInfo.email}
              </a>
            </div>
          </div>
          <div>
            <h3 className="text-lg font-semibold mb-4">{footerContent.social.title}</h3>
            <div className="flex space-x-4">
              <a href="https://facebook.com" className="text-gray-600 hover:text-primary">
                <Facebook className="w-6 h-6" />
              </a>
              <a href="https://instagram.com" className="text-gray-600 hover:text-primary">
                <Instagram className="w-6 h-6" />
              </a>
              <a href="https://linkedin.com" className="text-gray-600 hover:text-primary">
                <Linkedin className="w-6 h-6" />
              </a>
            </div>
          </div>
        </div>
        <div className="mt-8 pt-8 border-t border-gray-200">
          <p className="text-center text-gray-500">&copy; {siteInfo.copyright(new Date().getFullYear())}</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
