
import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/components/ui/use-toast';
import { contactContent } from '@/constants/content';

const ContactForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    email: '',
  });
  const { toast } = useToast();

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Here you would integrate with Google Forms
    toast({
      title: contactContent.form.successMessage.title,
      description: contactContent.form.successMessage.description,
    });
    setFormData({ name: '', phone: '', email: '' });
  };

  return (
    <div className="bg-white py-16">
      <div className="max-w-md mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-3xl font-playfair font-bold text-center mb-8">{contactContent.form.title}</h2>
        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label htmlFor="name" className="block text-sm font-medium text-gray-700">{contactContent.form.fields.name}</label>
            <Input
              required
              type="text"
              id="name"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="mt-1"
            />
          </div>
          <div>
            <label htmlFor="phone" className="block text-sm font-medium text-gray-700">{contactContent.form.fields.phone}</label>
            <Input
              required
              type="tel"
              id="phone"
              value={formData.phone}
              onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
              className="mt-1"
            />
          </div>
          <div>
            <label htmlFor="email" className="block text-sm font-medium text-gray-700">{contactContent.form.fields.email}</label>
            <Input
              required
              type="email"
              id="email"
              value={formData.email}
              onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              className="mt-1"
            />
          </div>
          <Button type="submit" className="w-full bg-primary hover:bg-primary/90">
            {contactContent.form.submitButton}
          </Button>
        </form>
      </div>
    </div>
  );
};

export default ContactForm;
