
import { useState } from 'react';
import { Link } from 'react-router-dom';
import { Button } from './ui/button';
import { Menu } from 'lucide-react';
import { useIsMobile } from '@/hooks/use-mobile';
import { navigation, siteInfo } from '@/constants/content';

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const isMobile = useIsMobile();

  const toggleMenu = () => setIsMenuOpen(!isMenuOpen);

  return (
    <nav className="bg-soft-purple shadow-sm fixed w-full z-10">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex-shrink-0 flex items-center">
            <Link to="/" className="font-playfair text-2xl font-bold text-primary">{siteInfo.name}</Link>
          </div>
          
          {isMobile ? (
            <div className="flex items-center">
              <Button variant="ghost" size="icon" onClick={toggleMenu} aria-label="Toggle menu">
                <Menu className="h-6 w-6" />
              </Button>
            </div>
          ) : (
            <div className="flex items-center space-x-8">
              <Link to="/" className="text-gray-700 hover:text-primary transition">{navigation.home}</Link>
              <Link to="/about" className="text-gray-700 hover:text-primary transition">{navigation.about}</Link>
              <Link to="/therapy" className="text-gray-700 hover:text-primary transition">{navigation.therapy}</Link>
              <Link to="/contact" className="text-gray-700 hover:text-primary transition">{navigation.contact}</Link>
            </div>
          )}
        </div>

        {/* Mobile menu dropdown */}
        {isMobile && isMenuOpen && (
          <div className="md:hidden bg-white border-t border-gray-200 rounded-xl my-4">
            <div className="flex flex-col space-y-4 py-4">
              <Link to="/" className="px-4 py-2 text-gray-700 transition" onClick={toggleMenu}>{navigation.home}</Link>
              <Link to="/about" className="px-4 py-2 text-gray-700 transition" onClick={toggleMenu}>{navigation.about}</Link>
              <Link to="/therapy" className="px-4 py-2 text-gray-700 transition" onClick={toggleMenu}>{navigation.therapy}</Link>
              <Link to="/contact" className="px-4 py-2 text-gray-700 transition" onClick={toggleMenu}>{navigation.contact}</Link>
            </div>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
